.list-gallery {
    width: 100%;
    height: 100vh;
    background : $cl-white;

    .close-button {
        position: absolute;
        top: $px-24;
        left: $px-24;
        z-index: 1;
        background: none;
    }

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;

        .gallery-header {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: $px-18;
            position: absolute;
            top: 0;
            left: 0;
            width: 5vw;
            height: 100vh;

            .gallery-navigation {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: center;
                gap: $px-8;
                height: 10vw;

                .button {
                    background: none;
                }

                p {
                    position: relative;
                    cursor: pointer;
                    transition: 0.3s ease;
                    color: $cl-black;
                    text-transform: capitalize;
                    text-align: center;
                }

                .pager {
                    display: flex;
                    justify-content: space-between;
                    width: 100%;

                    .button {
                        width: $px-24;
                        height: $px-24;

                        img {
                            width: $px-22;
                            height: $px-22;
                        }
                    }
                }
            }
        }

        .gallery-image {
            width: 90vw;
            height: 90vh;
            overflow: hidden;

            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

            .list-item-container {
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                left: 5vw;
                bottom: 0;
                width: 90vw;
                height: 5vh;

                .item-numbers {
                    display: flex;
                    align-items: center;
                    gap: $px-12;
                    
                    .item-number {
                        position: relative;
                        cursor: pointer;
                        color: $cl-gray02;
                        transition: 0.3s ease;

                        .mark {
                            position: absolute;
                            width: 100%;
                            height: 4px;
                            left: 0;
                            bottom: -4px;
                            opacity: 0;
                            transition: 0.3s ease;
                            background: $cl-black;
                        }

                        &:hover {
                            color: $cl-black;
                        }
                    }

                    .highlight {
                        color: $cl-black;
                        .mark {
                            opacity: 1;
                        }
                    }
                }

                .item-name {
                    color: $cl-black;
                    text-transform: capitalize;
                }
            }

    }
}

.list-gallery::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}